import React, { Component } from 'react';

class FormComponent extends Component {
    render() {
        return (
            <div className="col-md-4">
                        <div className="banner-form-agileinfo">
                          <h5>Need To <span>Transport</span>?</h5>
                          <p>Please us provide below details -</p>
                          <form action="#" method="post">
                            <input type="text" className="email" name="name" placeholder="Name" required="" />
                            <input type="tel" className="tel" name="tel" placeholder="Phone Number" required="true" />
                            <input type="text" className="email" name="email" placeholder="Email" required="" />
                            <input type="text" className="email" name="from" placeholder="Transport From" required="" />
                            <input type="text" className="email" name="to" placeholder="Transport To" required="" />
                            <select className="form-control option-w3ls">
                              <option>Load Type</option>
                              <option>Full Load</option>
                              <option>Part Load</option>
                            </select>
                            <input type="submit" className="hvr-shutter-in-vertical" value="Get started" />
                          </form>
                        </div>
                      </div>
        );
    }
}

export default FormComponent;
