import React, { Component } from 'react';
import BannerTop from './BannerTop';

class Home extends Component {
  render() {
    return (
      <div className="slider">
        <div className="callbacks_container">
          <ul className="rslides" id="slider">
            <li>
              <BannerTop id={1} title1={"Full/Part Loading to"} title2={"fill the truck space"} />
            </li>
            <li>
              <BannerTop id={2} title1={"Container/ tanker"} title2={"Booking System"} />
            </li>
            <li>
              <BannerTop id={3} title1={"OPT Water Transport"} title2={"Reduce Cost"} />
            </li>
            <li>
              <BannerTop id={4} title1={"Large Warehousing"} title2={"Stock Your Goods"} />
            </li>
          </ul>
        </div>
        <div className="clearfix"></div>
      </div>
    );
  }
}

export default Home;
